import parseISO from 'date-fns/parseISO'

class DateFormatOverridden extends Intl.DateTimeFormat {
  format (date) {
    const parts = super.formatToParts(date)
    const map = {}
    parts.forEach(item => {
      map[item.type] = item.value
    })
    const datePart = `${map.day}.${map.month}.${map.year}`
    if (map.hour) {
      const timePart = `${map.hour}:${map.minute}:${map.second}`
      return `${datePart} ${timePart}`
    }
    return datePart
  }
}

export default {
  install: function (Vue) {
    Intl.DateTimeFormat = DateFormatOverridden
    Vue.prototype.$date = function (value) { return value ? this.$d(value instanceof Date ? value : parseISO(value), 'short') : '' }
    Vue.prototype.$dateISO = function (value) { return value ? (value instanceof Date ? value : parseISO(value)).toISOString().split('T')[0] : '' }
    Vue.prototype.$dateTime = function (value) { return value ? this.$d(value instanceof Date ? value : parseISO(value), 'long') : '' }
  }
}
