<template>
  <div class="position-relative" :style="{zIndex: this.zIndex}">
    <div class="veera-modal"
         :class="{show: show, 'veera-modal-dialog-centered' : verticallyCentered }"
         v-show="show" style="display: block"
         tabindex="-1" role="dialog"
         :aria-labelledby="title"
         @click="cancel">
      <div class="veera-modal-dialog" :class="modalClass" role="document" @click.stop>
        <div class="veera-modal-content">
          <template v-if="title">
            <div class="veera-modal-header">
              <h4 class="veera-modal-title">{{ title }}</h4>
              <button class="veera-modal-dialog-close-btn"
                      @click="cancel"
                      type="button">
                <i class="material-icons notranslate">close</i>
              </button>
            </div>
          </template>
          <template v-else>
            <button type="button"
                    class="btn-close position-absolute end-0 me-3 mt-3"
                    style="z-index: 1"
                    data-bs-dismiss="modal"
                    :aria-label="cancelLabelOrDefault"
                    @click="cancel"
            ></button>
          </template>
          <div class="veera-modal-body" :class="{dimmer: loading}">
            <slot/>
          </div>
          <div class="veera-modal-footer" v-if="!hideFooter">
            <button type="button"
                    class="c-btn c-btn--secondary c-btn--mobile-block mr-md-2 mt-2 mt-md-0 order-2 order-md-1"
                    @click="cancel">{{ cancelLabelOrDefault }}</button>

            <button type="button"
                    class="c-btn c-btn--mobile-block order-1 order-md-2"
                    :disabled="!enableAccept"
                    :class="[
                        acceptClass ? acceptClass : 'c-btn--success',
                        { disabled: !enableAccept }
                    ]"
                    v-if="acceptLabel" @click="accept">
              <span>{{ acceptLabel }}</span>
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="veera-modal-backdrop show" v-show="show"></div>
  </div>
</template>

<script>
import store from '@/store'
import _ from 'lodash'

export default {
  props: {
    show: Boolean,
    title: String,
    cancelLabel: { type: String },
    acceptLabel: String,
    acceptClass: String,
    acceptIcon: String,
    closeOnAccept: { type: Boolean, default: true },
    verticallyCentered: Boolean,
    modalClass: { type: String, default: 'veera-modal-md' },
    loading: Boolean,
    hideFooter: Boolean,
    enableAccept: { type: Boolean, default: true }
  },
  data () {
    return {
      zIndex: _.uniqueId()
    }
  },
  mounted () {
    document.addEventListener('keyup', this.keyup)
  },
  beforeDestroy () {
    document.removeEventListener('keyup', this.keyup)
  },
  methods: {
    accept () {
      if (this.closeOnAccept) this.close()
      this.$emit('accept')
    },
    cancel () {
      this.close()
      this.$emit('cancel')
    },
    close () {
      this.$emit('update:show', false)
      store.closeSharedModal()
    },
    keyup (e) {
      if (this.show && e.keyCode === 27) this.cancel()
    },
    updateBodyClass () {
      document.body.classList[this.show ? 'add' : 'remove']('modal-open')
    }
  },
  created () {
    this.updateBodyClass()
  },
  watch: {
    show () {
      this.updateBodyClass()
    }
  },
  computed: {
    cancelLabelOrDefault () {
      return this.cancelLabel || 'Sulge'
    }
  }
}
</script>

