        </div>

        <div class="text-muted small mt-2" v-if="role.slug === 'STAT_ESTAT_RESPONDENT'">
<!--          Selle rolliga koos lisatakse sageli ka järgnevaid rolle. Kas soovid ka need lisada?-->
