        <div v-if="(role.roleDefinition !== undefined && role.roleDefinition.canSubDelegate)" class="form-check form-check-inline mt-2">
          <input class="form-check-input " type="checkbox" value="" :id="'canSubDelegate-' + uuid">
          <label class="form-check-label" :for="'canSubDelegate-' + uuid">
