</template>

<script>
import store from '@/store'
import VeeraCard from '@/components/VeeraCard'
import _ from 'lodash'
import SelectRole from '@/components/SelectRole'
/* import VeeraTooltip from '@/components/VeeraTooltip' */
import mockData from '@/mockData'

export default {
  name: 'RoleAddRow',
  components: { /* VeeraTooltip, */ SelectRole, VeeraCard },
  props: ['role', 'mode', 'addIndex', 'hasError'],
  data () {
    return {
      state: store.state,
      changeMode: null,
      lastsForever: true,
      selectedRoleSlug: null,
      uuid: _.uniqueId()
    }
  },
  created () {
    if (this.role) {
      this.selectedRoleSlug = this.role.slug
    }
  },
  methods: {
    updateNewRoleWithCorrectSlug (slug) {
      if (slug) {
        const newRoleToEdit = store.state.newRoles[store.state.newRoles.findIndex(i => i.id === this.role.id)]
        newRoleToEdit.slug = slug
        const roleDefinition = mockData.roleDescriptions[mockData.roleDescriptions.findIndex(desc => desc.slug === slug)]
        newRoleToEdit.roleDefinition = roleDefinition
      }
    },
    cancelRowAdding () {
      store.state.newRoles.splice(store.state.newRoles.findIndex(i => i.id === this.role.id), 1)
      store.state.roleChangesToBeCommitted.splice(store.state.roleChangesToBeCommitted.findIndex(i => i.id === this.role.id), 1)
    },
    addNewRoleForm (roleSlug) {
      store.addNewRoleToBeCommitted(roleSlug)
    },
    getRoleDetails (roleSlug) {
      return mockData.getRoleDescriptionBySlug(roleSlug)
    }
  }
}
</script>
