<template>
  <VeeraModal :show.sync="state.modals.editAccessRight"
         :title="'Esindaja volitused'"
         :accept-label="acceptButtonLabel"
         :cancel-label="closeButtonLabel"
         :closeOnAccept="false"
         @accept="submitChanges"
         @cancel="cancelRoleEditing"
         :enableAccept="hasThingsToSubmit"
  >

    <template v-if="state.currentUserId">
      <div class="row mt-n4">
        <div class="col-md-6 pb-3 pb-md-0">
          <template v-if="state.person">
            <div class="small text-muted mb-2">Esindaja</div>
            <h4 class="mb-4">
              <template v-if="state.viewerIsAgent">
                <span v-html="currentUserNameFormatted"></span>
              </template>
              <template v-else>
                <span v-html="currentPersonNameFormatted"></span>
              </template>
            </h4>
          </template>
          <template v-else>
            <div class="small text-muted mb-2">Esindaja</div>
            <SelectPerson
              v-model="selectedPerson"
              :selectedPerson="state.roleAddingSelectedPerson"
              :class="{'is-invalid' : userSubmitted && errorAgentNotSelected && !selectedPerson}"
            />
            <div class="invalid-feedback d-block mb-2" v-if="userSubmitted && errorAgentNotSelected && !selectedPerson">
              Palun vali esindaja
            </div>
          </template>
        </div>
        <div class="col-md-6">
          <div class="small text-muted mb-2">Esindatav</div>
          <h4 class="mb-4">
            <template v-if="state.viewerIsAgent">
              <span v-html="currentPersonNameFormatted"></span>
            </template>
            <template v-else-if="state.originalRoleGrantor !== null">
              <span v-html="originalGrantorNameFormatted"></span>
            </template>
            <template v-else>
              <span v-html="currentUserNameFormatted"></span>
            </template>
          </h4>
        </div>
      </div>

      <RoleEditRow v-for="(role, index) of state.roles" :role="role" v-bind:key="role.slug"
                   :editIndex="index"
      />

      <!--
      <template v-if="state.roleRecommendations && state.roleRecommendations.length > 0">
        <div class="mt-4 mb-2 d-flex">
          <i class="material-icons notranslate text-warning small mr-2 mt-1">lightbulb</i>
          <div>
            <div class="font-weight-bold">Soovitatud rollid</div>
            <div class="small text-muted mb-2">
              Kaalu ka nende rollide lisamist. Teised sarnased ettevõtted lisavad sageli just selliseid volitusi
            </div>
          </div>
        </div>

        <RoleEditRow v-for="(roleRecommendation, index) of state.roleRecommendations"
                     :role="roleRecommendation"
                     :recommendation="true"
                     v-bind:key="roleRecommendation.id"
                     :change-mode="state.roleRecommendationEditModes[index]"
        />
      </template>
      -->

      <RoleAddRow v-for="(role, index) of newRolesAddedManually"
                  :role="role"
                  v-bind:key="role.index"
                  :add-index="index"
                  :has-error="userSubmitted && !role.slug"
      />

      <template v-if="!hasCLevelRole">
        <div class="my-4 d-flex justify-content-between">
          <div>
            <template v-if="!state.viewerIsAgent && state.originalRoleGrantor == null">
            <a href="#" @click="addNewRoleForm()" class="d-inline-flex text-decoration-none">
              <i class="material-icons notranslate">add</i>
              <span>Lisa volitus</span></a>
            </template>
          </div>
          <div v-if="state.roles && hasAtLeastOneRemovableRole">
            <a href="#" @click="removeAllRoles(state.roles)" class="text-danger d-inline-flex text-decoration-none">
              <template v-if="state.viewerIsAgent">
                Loobu kõigist
              </template>
              <template v-else>
                Eemalda kõik
              </template>
            </a>
          </div>
        </div>
      </template>

    </template>

<!--    {{ state.roleChangesToBeCommitted}}-->
<!--    <pre class="text-muted small">{{state.newRoles}}</pre>-->

  </VeeraModal>
</template>

<script>
import store from '@/store'
import mockData from '@/mockData'
import VeeraModal from '../components/VeeraModal'
import RoleEditRow from '@/components/RoleEditRow'
import RoleAddRow from '@/components/RoleAddRow'
import SelectPerson from '@/components/SelectPerson'

export default {
  name: 'EditAccessRightModal',
  components: { SelectPerson, RoleAddRow, RoleEditRow, VeeraModal },
  data () {
    return {
      state: store.state,
      currentCompany: null,
      selectedPerson: null,
      userSubmitted: false,
      addingCompleted: false
    }
  },
  methods: {
    addNewRoleForm () {
      store.addNewRoleToBeCommitted()
    },
    removeAllRoles (roles) {
      roles.map(role => {
        store.state.roleChangesToBeCommitted.push(
          {
            mode: 'remove',
            id: role.id
          }
        )
      })
    },
    startSigning () {
      store.openSharedModal('signingModal')
    },
    submitChanges () {
      this.userSubmitted = true
      store.state.roleChangesToBeCommitted.map(change => {
        // console.log('about to commit this', role)
        if (change.mode === 'add') {
          // console.log('entered adding flow')
          if (!this.addingCompleted) {
            if (!this.hasErrors) {
              console.log('items in new roles state added maually', this.newRolesAddedManually.length)
              this.newRolesAddedManually.map(newRole => {
                const newRepresentationRight = {
                  id: Math.floor(Math.random() * 10000),
                  grantorId: store.state.currentUserId,
                  agentId: this.selectedPerson ? this.selectedPerson.value : store.state.person.id,
                  slug: newRole.slug,
                  validFrom: '2020-11-06',
                  validUntil: '2021-12-31'
                }
                mockData.representationRights.push(newRepresentationRight)
                console.log('adding via manual adding', newRepresentationRight)
              })
            }
            this.addingCompleted = true
          }
        } else if (change.mode === 'addRecommendation') {
          console.log('items in new roles state from recommendations', this.newRolesAddedViaRecommendations.length)
          this.newRolesAddedViaRecommendations
            .filter(x => x.id === change.id)
            .map(newRole => {
              const newRepresentationRight = {
                id: Math.floor(Math.random() * 10000),
                grantorId: store.state.currentUserId,
                agentId: store.state.person.id,
                slug: newRole.slug,
                validFrom: '2020-11-06',
                validUntil: '2021-12-31'
              }
              mockData.representationRights.push(newRepresentationRight)
              console.log('adding from recommendation', newRepresentationRight)
              mockData.discardRepresentationRightRecommendation(change.id)
              console.log('discarding existing recommendation', change.id)
            })
        } else if (change.mode === 'remove') {
          const index = mockData.representationRights.findIndex(i => i.id === change.id)
          if (index >= 0) {
            mockData.representationRights.splice(index, 1)
          }
        } else {
          console.log('nothing to do for role ', change.id)
        }
      })
      if (!this.hasErrors) {
        this.goToSigningFlow()
      }
    },
    cancelRoleEditing () {
      store.resetAccessRightModifications()
    },
    closeModalWithSuccess () {
      this.selectedPerson = null
      store.resetAccessRightModifications()
      store.closeSharedModal('editAccessRight')
      store.addToast('SUCCESS', 'Volituste muutmine oli edukas')
      this.userSubmitted = false
      this.addingCompleted = false
    },
    goToSigningFlow () {
      store.openSharedModal('signingModal')
      this.userSubmitted = false
      this.addingCompleted = false
    }
  },
  computed: {
    newRolesAddedManually () {
      return store.state.newRoles.filter(x => !x.addedFromRecommendation)
    },
    newRolesAddedViaRecommendations () {
      return store.state.newRoles.filter(x => x.addedFromRecommendation)
    },
    hasCLevelRole () {
      return this.state.person && this.state.roles[0].details.slug === 'BUSINESS_BOARD_MEMBER'
    },
    // TODO does anyone use it?
    hasUnchangeableRole () {
      return this.state.person && this.state.roles.filter(x => !x.details.canEdit).length > 0
    },
    hasAtLeastOneRemovableRole () {
      return /* this.state.person && */this.state.roles.length >= 2 && this.state.roles.filter(x => x.details.canRemove).length > 0
    },
    hasThingsToSubmit () {
      return this.state.roleChangesToBeCommitted.length > 0
    },
    currentUserNameFormatted () {
      return mockData.getPersonNameFormattedById(store.state.currentUserId)
    },
    currentPersonNameFormatted () {
      return mockData.getPersonNameFormattedById(store.state.person.id)
    },
    originalGrantorNameFormatted () {
      return mockData.getPersonNameFormattedById(store.state.originalRoleGrantor.id)
    },
    closeButtonLabel () {
      return this.hasThingsToSubmit ? 'Tühista' : 'Sulge'
    },
    acceptButtonLabel () {
      return this.hasThingsToSubmit ? 'Kinnita' : null
    },
    errorAgentNotSelected () {
      return !this.selectedPerson && !store.state.person
    },
    hasErrors () {
      const hasUnselectedRoles = store.state.newRoles.filter(x => !x.slug).length > 0
      // console.log('checking for errors, has unselected roles: ', hasUnselectedRoles, 'agentNotselected: ', this.errorAgentNotSelected)
      return this.errorAgentNotSelected || hasUnselectedRoles
    }
  }
}
</script>
