<template>
  <VeeraModal :show.sync="state.modals.authenticationModal"
              :title="'Autentimise samm'"
              :closeOnAccept="false"
              :modal-class="'veera-modal-md'"
              :vertically-centered="true"
              :accept-label="'Jätka'"
              @accept="mockSuccessfulLogin"
  >
    <a @click="mockSuccessfulLogin">
      <div class="text-center">
        <p>Tavaolukorras toimuks siin autentimine.</p>
        <img src="mock/autentimisteenus.png" class="img-fluid" style="opacity: 0.6; width: 66%;">
      </div>
    </a>
  </VeeraModal>
</template>

<script>
import VeeraModal from '@/components/VeeraModal'
import store from '@/store'
export default {
  name: 'AuthenticationModal',
  components: { VeeraModal },
  data () {
    return {
      state: store.state
    }
  },
  methods: {
    mockSuccessfulLogin () {
      store.state.currentUserAuthenticated = true
      store.state.modals.authenticationModal = false
      this.$router.push({ name: 'UserRoleSelectionPage' })
    }
  }
}
</script>
