const store = {
  state: {
    currentUserId: null,
    currentUserAuthenticated: false,
    agent: null,
    grantor: null,
    person: null,
    roles: null,
    roleRecommendations: null,
    newRoles: [],
    roleAddingSelectedPerson: null,
    roleAddingSelectedPersonDetails: null,
    roleChangesToBeCommitted: [],
    roleRecommendationEditModes: [],
    viewerIsAgent: false,
    originalRoleGrantor: null,
    modals: {
      editAccessRight: false,
      signingModal: false,
      authenticationModal: false
    },
    toasts: []
  },

  dismissToast (id) {
    this.state.toasts.splice(this.state.toasts.findIndex(toast => toast.id === id), 1)
  },

  addToast (type, title) {
    const id = Math.random()
    this.state.toasts.push(
      {
        id: id,
        type: type,
        title: title
      }
    )
    if (type === 'SUCCESS') {
      setTimeout(() => { this.dismissToast(id) }, 7000)
    }
  },

  openSharedModal (key) {
    this.state.modals[key] = true
  },

  closeSharedModal (key) {
    this.state.modals[key] = false
  },
  resetAccessRightModifications () {
    store.state.person = null
    store.state.viewerIsAgent = false
    store.state.originalRoleGrantor = null
    store.state.roles = null
    store.state.roleRecommendations = null
    store.state.newRoles = []
    store.state.roleRecommendationEditModes = []
    store.state.roleChangesToBeCommitted = []
    store.state.roleAddingSelectedPerson = null
    store.state.roleAddingSelectedPersonDetails = null
  },
  setNewAccessRights () {
    store.state.person = null
    store.state.roles = null
    const newRoleId = Math.floor(Math.random() * 10000)
    store.state.roleChangesToBeCommitted.push(
      {
        mode: 'add',
        id: newRoleId
      }
    )
    store.state.newRoles = [{
      id: newRoleId,
      grantorId: store.state.currentUserId,
      slug: null,
      validFrom: null,
      validUntil: null
    }]
  },
  changeCurrentPerson (newPersonId) {
    this.state.currentUserId = newPersonId
  },

  setRecommendationAsANewRoleToBeAdded (role) {
    this.state.newRoles.push({
      id: role.id,
      grantorId: store.state.currentUserId,
      slug: role.details.slug,
      validFrom: new Date().toISOString().slice(0, -14),
      validUntil: '2021-12-31',
      addedFromRecommendation: true
    })
  },
  addNewRoleToBeCommitted (roleSlug) {
    const newRoleId = Math.floor(Math.random() * 10000)
    console.log('adding role with id:', newRoleId)
    store.state.roleChangesToBeCommitted.push(
      {
        mode: 'add',
        id: newRoleId
      }
    )
    store.state.newRoles.push({
      id: newRoleId,
      grantorId: store.state.currentUserId,
      slug: roleSlug,
      validFrom: '2020-11-06',
      validUntil: '2021-12-31'
    })
  }
}

export default store
