<template>
  <div>
    <template v-if="getAgentsForCurrentUser.length > 0">
      <div class="d-flex justify-content-between flex-column flex-md-row mb-4">
        <div>
          <button type="button" class="c-btn c-btn--primary mb-3 mb-md-0"
                  @click.prevent="openNewAccessRightModal()"
          >Lisa uus volitus
          </button>
        </div>
        <div>
          <button type="button" class="c-btn c-btn--text c-btn--icon text-decoration-none mt-2"
                  @click="filtersOpened = !filtersOpened"
          >
            <i class="material-icons notranslate mr-1">search</i>
            <template v-if="filtersOpened">Sulge otsing</template>
            <template v-else>Ava otsing</template>
          </button>
        </div>
      </div>
      <template v-if="filtersOpened">
        <PersonListFilter />
      </template>
      <PersonRepresentationCard
        v-for="(person, index) of getAgentsForCurrentUser"
        :person="person.agent"
        :roles="person.roles"
        :role-recommendations="person.roleRecommendations"
        v-bind:key="'person' + index + '-' + forceReRenderKey"
        :class="{'mb-3' : index !== getAgentsForCurrentUser.length - 1}"
      />
    </template>
    <template v-else>
      <div class="text-center p-5">
        <p>Ühtegi volitust pole veel määratud</p>
        <button type="button" class="c-btn c-btn--primary mb-4 mb-md-0"
                @click.prevent="openNewAccessRightModal()"
        >Lisa uus volitus</button>
      </div>
    </template>
  </div>
</template>

<script>
import PersonRepresentationCard from '@/components/PersonRepresentationCard'
import store from '@/store'
import mockData from '@/mockData'
import _ from 'lodash'
import PersonListFilter from '@/components/PersonListFilter'

export default {
  name: 'GrantorAgents',
  components: {
    PersonListFilter,
    PersonRepresentationCard
  },
  props: ['personalCode'],
  data () {
    return {
      state: store.state,
      mockData: mockData,
      searchString: null,
      forceReRenderKey: 0,
      filtersOpened: false
    }
  },
  methods: {
    openNewAccessRightModal () {
      store.setNewAccessRights()
      store.openSharedModal('editAccessRight')
    },
    forceRerender () {
      this.forceReRenderKey += 1
    }
  },
  computed: {
    getAgentsForCurrentUser () {
      this.forceRerender()
      const agents = _.uniq(mockData.representationRights
        .filter(representationRight => representationRight.grantorId === this.state.currentUserId)
        .map(representationRight => representationRight.agentId))
        .map(agentId => {
          return {
            agent: mockData.getPersonById(agentId),
            roles: mockData.representationRights.filter(
              representationRight => representationRight.grantorId === this.state.currentUserId &&
                representationRight.agentId === agentId
            ).map(role => {
              return {
                id: role.id,
                validFrom: role.validFrom,
                validFromInTheFuture: role.validFromInTheFuture,
                validUntil: role.validUntil,
                details: mockData.getRoleDescriptionBySlug(role.slug)
              }
            }),
            roleRecommendations: mockData.representationRightsRecommendations.filter(
              representationRight => representationRight.grantorId === this.state.currentUserId &&
                representationRight.agentId === agentId
            ).map(role => {
              return {
                id: role.id,
                validFrom: role.validFrom,
                validUntil: role.validUntil,
                details: mockData.getRoleDescriptionBySlug(role.slug)
              }
            })
          }
        })
      return agents
    }
  }
}
</script>
