<template>
  <div class="veera-collapse">
    <button class="collapse-button" @click="toggleCollapse(open)">
      <div class="header-title" :class="{ open: open }">
        <div class="collapse-header">
          <div class="header-left has-arrow" :class="{ opened: open }">
            <h3 class="header-title mb-0">
              <slot name="collapse-header" />
            </h3>
          </div>
          <div class="header-right">
            <button type="button" class="header-button position-relative">
              <span class="d-none d-lg-block">
                <template v-if="open">Sulge</template>
                <template v-else>Ava</template>
              </span>
            </button>
          </div>
        </div>
      </div>
    </button>
    <div class="collapsible-wrapper" :class="{ 'veera-collapsed': !open}">
      <div class="collapse-content">
        <div class="d-block w-100 mb-2">
          <VeeraLoadingSimple v-if="simulateLoadingDelay"/>
          <slot name="veera-collapse-body" v-else />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VeeraLoadingSimple from './VeeraLoadingSimple'
export default {
  name: 'VeeraCollapse',
  components: { VeeraLoadingSimple },
  data () {
    return {
      open: false,
      simulateLoadingDelay: false
    }
  },
  methods: {
    toggleCollapse (currentState) {
      this.open = !this.open
      if (this.open) {
        this.simulateLoadingDelay = true
        setTimeout(() => {
          this.simulateLoadingDelay = false
        }, 1000)
      }
    }
  }
}
</script>

