<template>
  <form novalidate="">
    <div class="form-group row">
      <label class="col-md-5 col-form-label text-md-right">Otsi isikut või ettevõtet</label>
      <div class="col-md-5">
        <input id="role"
               type="search"
               class="form-control"
               placeholder="Nimi, isikukood või reg.nr"
        >
      </div>
    </div>
    <div class="form-group row">
      <label class="col-md-5 text-md-right">Filtreeri rolli järgi</label>
      <div class="col-md-5">
        <div v-for="(role, index) of roleOptions" :key="role.id">
          <div class="custom-control custom-checkbox">
            <input type="checkbox" class="custom-control-input" :id="'role-filter-' + index">
            <label class="custom-control-label" :for="'role-filter-' + index">{{role.title}}</label>
          </div>
        </div>
      </div>
    </div>
    <div class="form-group row">
      <div class="offset-md-5 col-md-5">
        <button type="button" class="c-btn c-btn--primary mb-3 mb-md-0">Otsi</button>
      </div>
    </div>
  </form>
</template>

<script>
import mockData from '@/mockData'
export default {
  name: 'PersonListFilter',
  computed: {
    roleOptions () {
      return mockData.roleDescriptions
        .map(role => {
          return {
            title: role.namespace + ': ' + role.title,
            value: role.slug
          }
        })
    }
  }
}
</script>
