<template>
  <div>
    <template v-if="shouldShowRoleAsStaticBlock">
      <div class="form-control h-auto d-flex align-items-center bg-light">
        <div>
          {{selectedRoleDetails.namespace}}: <strong>{{ selectedRoleDetails.title}}</strong>
        </div>
        <VeeraTooltip v-if="selectedRoleDetails.description" class="d-inline-flex ml-2">
          {{selectedRoleDetails.description}}
        </VeeraTooltip>
        <div class="ml-auto my-n2 mr-n2">
          <!-- when sub-delegating then this button should not be visible -->
          <button type="button"
                  class="c-btn c-btn--link c-btn--icon c-btn--icon-only c-btn--sm"
                  aria-label="Muuda"
                  @click.prevent="changeSelectedRole()"
          >
            <i class="material-icons notranslate">edit</i>
          </button>
        </div>
      </div>
    </template>
    <template v-else>
<!--      v-model="userSelectedRole"-->
<!--      v-on:change="newValue => roleChanged(newValue)"-->
<!--      @change="receivedRoleChangeFromDropdown()"-->
      <VeeraDropdown :options="roleOptions"
                     :selected-option="userSelectedRole"
                     :auto-focus="autoFocusRequested"
                     @change="x => receivedRoleChangeFromDropdown(x)"
                     placeholder="Vali või otsi roll..."
      />
    </template>
  </div>
</template>

<script>
import mockData from '@/mockData'
import VeeraDropdown from '@/components/VeeraDropdown'
import VeeraTooltip from '@/components/VeeraTooltip'
import _ from 'lodash'
export default {
  name: 'SelectRole',
  components: { VeeraTooltip, VeeraDropdown },
  props: ['selectedRoleSlug'],
  data () {
    return {
      selectedRoleDetails: {},
      userSelectedRole: {},
      autoFocusRequested: false
    }
  },
  created () {
    if (this.selectedRoleSlug) {
      this.getRoleDetails(this.selectedRoleSlug)
    }
  },
  methods: {
    changeSelectedRole () {
      this.selectedRoleDetails = {}
      this.autoFocusRequested = true
    },
    getRoleDetails (slug) {
      this.selectedRoleDetails = mockData.getRoleDescriptionBySlug(slug)
    },
    receivedRoleChangeFromDropdown (newVal) {
      if (newVal.value) {
        this.getRoleDetails(newVal.value)
        this.$emit('change', newVal.value)
      }
    },
    compare (a, b) {
      if (a.title < b.title) { return -1 }
      if (a.title > b.title) { return 1 }
      return 0
    }
  },
  computed: {
    shouldShowRoleAsStaticBlock () {
      return !_.isEmpty(this.selectedRoleDetails)
    },
    roleOptions () {
      return mockData.roleDescriptions
        //    .filter(x => !x.assignedByLaw)
        .filter(x => x.canAdd)
        .map(role => {
          return {
            title: role.namespace + ': ' + role.title,
            value: role.slug,
            description: role.description
          }
        })
        .sort(this.compare)
    }
  }
}
</script>
