<template>
  <div>
  <veera-dropdown label="Minu roll"
                  :select-only="true"
                  :options="mockUserPersonsWithLogout"
                  v-model="currentUserPerson"
  ></veera-dropdown>
  </div>
</template>

<script>
import VeeraDropdown from './VeeraDropdown'
import store from '../store'
import mockData from '../mockData'
export default {
  name: 'UserDropdown',
  components: { VeeraDropdown },
  data () {
    return {
      state: store.state,
      userIdFromState: store.state.currentUserId,
      currentUserPerson: null
    }
  },
  watch: {
    currentUserPerson () {
      if (this.currentUserPerson.value === 'logOut') {
        store.state.currentUserId = null
        store.state.currentUserAuthenticated = false
        this.$router.push({ name: 'HomePage' })
      } else {
        store.state.currentUserId = this.currentUserPerson.value
      }
    }
  },
  created () {
    this.currentUserPerson = this.currentUserOptionObject
  },
  computed: {
    mockUserPersonsWithLogout () {
      const mockUserPersons = [...mockData.mockUserPersons]
      mockUserPersons.push(
        { title: 'Logi välja', value: 'logOut' }
      )
      return mockUserPersons
    },
    currentUserOptionObject () {
      return mockData.mockUserPersons.find(user => user.value === store.state.currentUserId)
    }
  }
}
</script>
