<script>
import store from '@/store'
import VeeraCard from '@/components/VeeraCard'
import _ from 'lodash'
import VeeraTooltip from '@/components/VeeraTooltip'
import mockData from '@/mockData'

export default {
  name: 'RoleEditRow',
  components: { VeeraTooltip, VeeraCard },
  props: ['role', 'mode', 'recommendation'],
  data () {
    return {
      state: store.state,
      lastsForever: this.role.validUntil === null || this.recommendation,
      uuid: _.uniqueId()
    }
  },
  computed: {
    currentChangeMode () {
      const getMode = store.state.roleChangesToBeCommitted.find(x => x.id === this.role.id)?.mode
      return getMode || false
    }
  },
  methods: {
    openSharedModal (key) {
      store.openSharedModal(key)
    },
    discardRecommendation (role) {
      mockData.discardRepresentationRightRecommendation(role.id) // remove from general mock data
      console.log('before', store.state.roleRecommendations)
      store.state.roleRecommendations.splice(store.state.roleRecommendations.findIndex(i => i.id === role.id), 1) // remove from modal context
      console.log('after', store.state.roleRecommendations)
    },
    changeRoleEditMode (newMode, role) {
      console.log('changing/adding recommendation', newMode, role)
      store.state.roleChangesToBeCommitted.push(
        {
          mode: newMode,
          id: role.id
        }
      )
      if (newMode === 'addRecommendation') {
        store.setRecommendationAsANewRoleToBeAdded(role)
      }
    },
    cancelRoleEditMode (role) {
      const changesContainingThisRole = store.state.roleChangesToBeCommitted.filter(x => x.id === role.id)
      changesContainingThisRole.forEach(f => store.state.roleChangesToBeCommitted.splice(store.state.roleChangesToBeCommitted.findIndex(i => i.id === f.id), 1))
      const newRolesContainingThisRole = store.state.newRoles.filter(x => x.id === role.id)
      newRolesContainingThisRole.forEach(f => store.state.newRoles.splice(store.state.newRoles.findIndex(i => i.id === f.id), 1))
    }
  }
}
</script>
