<template>
  <VeeraCard class="card-clickable p-0 veera-card-highlight-links-on-hover">
    <a class="d-block px-3 py-2 mb-1 no-decoration" href="#" tabindex="" @click.stop.prevent="openPersonModal('editAccessRight')">
      <div class="row">
        <div class="col-md-6 col-xl-4">
          <div class="d-inline-flex mb-3 mb-lg-0">
            <i class="material-icons text-muted notranslate lead mr-1">
              <template v-if="person.legalEntity">business</template><template v-else>person</template>
            </i>
            <div class="text-body">
              <strong>{{person.legalName ? person.legalName : (person.firstName + ' ' + (person.lastName))}}</strong>
              ({{person.id}})
            </div>
          </div>
        </div>
        <div class="col-md-6 col-xl-5">
          <div v-for="role of rolesUniqueOnly"
               v-bind:key="role.slug"
          >
<!--            <pre class="text-muted small">{{role}}</pre>-->
            <p :class="{'text-muted' : role.validFromInTheFuture}">
              {{role.details.namespace}}: <strong>{{role.details.title}}</strong>
              <VeeraTooltip v-if="role.details.description"
                            class="ml-1 d-inline-flex align-text-top">
                {{role.details.description}}
              </VeeraTooltip>
              <span v-if="role.validFromInTheFuture"> (alates {{ $d(new Date(role.validFrom)) }})</span>
            </p>
          </div>

        </div>
        <div class="offset-md-6 col-md-6 offset-xl-0 col-xl-3 pt-3 pt-xl-0 text-xl-right">
          <template v-if="assignedToMe">
            <template v-if="onlyShowViewButton">
              <a href="#" @click="openPersonModal('editAccessRight')">Vaata</a>
            </template>
            <template v-else>
              <a href="#" @click.stop="openSubDelegates()">Vaata edasivolitusi</a>
              <a href="#" class="mr-2 mr-md-0 ml-md-2" @click.stop="copyAccessRightsModal(roles, person)">Volita edasi</a>
              <a href="#" class="mr-2 mr-md-0 ml-md-2" @click.stop="openPersonModalWithAllRolesToBeRemoved(roles.length)">Loobu</a>
            </template>
          </template>
          <template v-else>
            <template v-if="onlyShowViewButton">
              <a href="#" @click="openPersonModal('editAccessRight')">Vaata</a>
            </template>
            <template v-else-if="showViewAndRemoveButtons">
              <a href="#" class="mr-2 mr-md-0 ml-md-2" @click="openPersonModal('editAccessRight')">Vaata</a>
              <a href="#" class="mr-2 mr-md-0 ml-md-2" @click.stop="openPersonModalWithAllRolesToBeRemoved(roles.length)">Eemalda</a>
            </template>
            <template v-else>
              <a href="#" class="mr-2 mr-md-0 ml-md-2" @click="openPersonModal('editAccessRight')">Muuda</a>
              <a href="#" class="mr-2 mr-md-0 ml-md-2" @click.stop="copyAccessRightsModal(roles, null)">Kopeeri</a>
              <a href="#" class="mr-2 mr-md-0 ml-md-2" @click.stop="openPersonModalWithAllRolesToBeRemoved(roles.length)">Eemalda</a>
            </template>
          </template>
        </div>
      </div>
    </a>
  </VeeraCard>
</template>

<script>
import VeeraCard from '@/components/VeeraCard'
import store from '@/store'
import VeeraTooltip from '@/components/VeeraTooltip'
import _ from 'lodash'
export default {
  name: 'PersonRepresentationCard',
  components: { VeeraTooltip, VeeraCard },
  props: ['person', 'roles', 'roleRecommendations', 'assignedToMe'],
  data () {
    return {
      state: store.state,
      localRoleRecommendations: this.roleRecommendations
    }
  },
  computed: {
    hasCLevelRole () {
      return this.roles[0].details.slug === 'BUSINESS_BOARD_MEMBER' || (this.roles[0].details.slug === 'BUSINESS_BOARD_MEMBER_JOINT' && this.roles.length === 0) || this.roles[0].details.slug === 'BUSINESS_PROCURATOR'
    },
    onlyShowViewButton () {
      if (this.roles.length === 0) {
        return false
      }
      for (let i = 0; i < this.roles.length; i++) {
        if (this.roles[i].details.canEdit || this.roles[i].details.canRemove || this.roles[i].details.canSubDelegate) {
          return false
        }
      }
      return true
    },
    showViewAndRemoveButtons () {
      if (this.roles.length === 0) {
        return false
      }
      for (let i = 0; i < this.roles.length; i++) {
        if (this.roles[i].details.canEdit) {
          return false
        }
      }
      return true
    },
    rolesUniqueOnly () {
      // return this.roles
      return _.uniqBy(this.roles, 'details.slug')
    }
  },
  methods: {
    openSubDelegates () {
      alert('not implemented')
    },
    openSharedModal (key) {
      store.openSharedModal(key)
    },
    openPersonModal (key) {
      store.state.person = this.person
      store.state.viewerIsAgent = this.assignedToMe
      store.state.originalRoleGrantor = null
      store.state.roles = this.roles
      store.state.roleRecommendations = this.roleRecommendations
      store.openSharedModal(key)
    },
    openPersonModalWithAllRolesToBeRemoved (rolesToBeRemovedCount) {
      store.state.person = this.person
      store.state.roles = this.roles
      store.state.roleRecommendations = this.roleRecommendations
      store.state.newRoles = []
      this.roles.map(role => {
        store.state.roleChangesToBeCommitted.push(
          {
            mode: 'remove',
            id: role.id
          }
        )
      })
      store.openSharedModal('editAccessRight')
    },
    copyAccessRightsModal (roles, representee) {
      store.state.originalRoleGrantor = representee
      store.state.person = null
      store.state.roles = null
      store.state.roleRecommendations = null
      // store.state.representeeId = representee.id
      roles.map(role => {
        // TODO somehow we should indicate that we excluded stuff
        if (!role.details.slug.startsWith('BUSINESS_')) {
          store.state.roleChangesToBeCommitted.push(
            {
              mode: 'add',
              id: role.id
            }
          )
          store.state.newRoles.push({
            id: Math.floor(Math.random() * 10000),
            // "Volita edasi" puhul on see midagi muud
            grantorId: store.state.currentUserId,
            slug: role.details.slug,
            validFrom: '2021-12-01',
            validUntil: null
          })
        }
      })
      store.openSharedModal('editAccessRight')
    }
  }
}
</script>
