<template>
  <VeeraModal :show.sync="state.modals.signingModal"
              :title="'Allkirjasta volituste muudatused'"
              cancel-label="Katkesta"
              :modal-class="'veera-modal-sm'"
              :vertically-centered="true"
              :hide-footer="true"
  >

      <div class="text-center mt-n2">
        <div class="py-4 rounded bg-light mb-3">
          <h1 class="mb-3 mt-n2">6817</h1>
          <VeeraSpinner />
        </div>
        <p class="mb-0">Kui ülaltoodud
        kood on sama, mida kuvatakse teie seadme ekraanil, sisestage oma <strong>PIN2 kood</strong>
        </p>
      </div>

    <div class="veera-modal-footer justify-content-center">
      <button type="button" class="c-btn c-btn--secondary" @click="cancel">Katkesta</button>
    </div>

    <div class="text-muted text-center small my-3 px-5">
      Tavaolukorras toimuks siin allkirjastamine ID-kaardiga, Mobiil-ID-ga või Smart ID-ga.<br />
      <a href="#" @click.prevent="completeSigningWithSuccess">Vajuta siia, et simuleerida allkirjastamist</a>
    </div>
  </VeeraModal>
</template>

<script>
import VeeraModal from '@/components/VeeraModal'
import store from '@/store'
import VeeraSpinner from '@/components/VeeraSpinner'

export default {
  name: 'SigningModal',
  components: { VeeraSpinner, VeeraModal },
  data () {
    return {
      state: store.state
    }
  },
  methods: {
    cancel () {
      store.closeSharedModal('signingModal')
    },
    completeSigningWithSuccess () {
      this.selectedPerson = null
      store.resetAccessRightModifications()
      store.closeSharedModal('editAccessRight')
      store.closeSharedModal('signingModal')
      store.addToast('SUCCESS', 'Volituste muutmine oli edukas')
    }
  }
}
</script>
