  getPersonById (id) {
    return this.persons.find(x => x.id === id)
  },
  getPersonNameFormattedById (id) {
    const person = this.persons.find(x => x.id === id)
    return '<strong>' + (person.legalName ? person.legalName : (person.firstName + ' ' + (person.lastName))) + '</strong>' +
      ' (' + person.id + ')'
  },

  getRoleDescriptionBySlug (slug) {
    return this.roleDescriptions.find(x => x.slug === slug)
  }
