<template>
  <div>
    <PersonRepresentationCard
      v-for="(person, index) of getGrantorsForCurrentUser"
      :person="person.grantor"
      :roles="person.roles"
      :role-recommendations="person.roleRecommendations"
      v-bind:key="person.id"
      :assigned-to-me="true"
      :class="{'mb-3' : index !== getGrantorsForCurrentUser.length - 1}"
    />
  </div>
</template>

<script>
import store from '@/store'
import mockData from '@/mockData'
import PersonRepresentationCard from '@/components/PersonRepresentationCard'
import _ from 'lodash'

export default {
  name: 'PersonsWhomTheSubjectCanRepresent',
  components: {
    PersonRepresentationCard
  },
  data () {
    return {
      state: store.state,
      mockData: mockData
    }
  },
  computed: {
    getGrantorsForCurrentUser () {
      const grantors = _.uniq(mockData.representationRights
        .filter(representationRight => representationRight.agentId === this.state.currentUserId)
        .map(representationRight => representationRight.grantorId))
        .map(grantorId => {
          return {
            grantor: mockData.getPersonById(grantorId),
            roles: mockData.representationRights.filter(
              representationRight => representationRight.agentId === this.state.currentUserId &&
                representationRight.grantorId === grantorId
            ).map(role => {
              return {
                id: role.id,
                validFrom: role.validFrom,
                validFromInTheFuture: role.validFromInTheFuture,
                validUntil: role.validUntil,
                details: mockData.getRoleDescriptionBySlug(role.slug)
              }
            }),
            roleRecommendations: mockData.representationRightsRecommendations.filter(
              representationRight => representationRight.grantorId === this.state.currentUserId &&
                representationRight.grantorId === grantorId
            ).map(role => {
              return {
                id: role.id,
                validFrom: role.validFrom,
                validUntil: role.validUntil,
                details: mockData.getRoleDescriptionBySlug(role.slug)
              }
            })
          }
        })
      return grantors
    }
  }
}
</script>
