<template>
  <div>
    <ul class="veera-tabs" role="tabslist">
      <li class="veera-tab-item"
          v-for="(tab, index) of tabs"
          role="presentation"
          :key="tab.index"
          @click="selectTab(index)"
      >
        <button type="button"
                class="veera-tab-btn"
                :class='{"active": (index === selectedIndex)}'
                role="tab" id="tab-0" aria-controls="tabpanel"
                aria-selected="">{{tab.heading}}</button>
      </li>
    </ul>
    <div class="veera-tab-content"
         :class="{'tab-active-0' : (selectedIndex === 0)}"
    >
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VeeraTabs',
  data () {
    return {
      selectedIndex: 0,
      tabs: []
    }
  },
  created () {
    this.tabs = this.$children
  },
  mounted () {
    this.selectTab(0)
  },
  methods: {
    selectTab (i) {
      this.selectedIndex = i
      this.tabs.forEach((tab, index) => {
        tab.isActive = (index === i)
      })
    }
  }
}
</script>

<style>
.veera-tabs {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  margin-bottom: -1px
}

.veera-tab-btn {
  background: none;
  border: none;
  padding: 16px;
  font-size: 16px;
  color: #00090f;
  cursor: pointer
}

.veera-tab-btn.active {
  background-color: #ffffff;
  border-radius: 5px 5px 0 0;
  color: #005aa3;
  border: 1px solid #dbdfe2;
  border-width: 1px 1px 0 1px
}

.veera-tab-content {
  background-color: #ffffff;
  padding: 16px;
  border: 1px solid #dbdfe2;
  border-radius: 5px
}

.veera-tab-content.tab-active-0 {
  border-radius: 0 5px 5px 5px
}
</style>
