<template>
<div>

  <h1 class="mb-3">Vali roll, et jätkata iseteeninduses</h1>

  <div
             v-for="(persons, index) of personCategories"
             :key="persons.id"
  >
    <h6 class="text-uppercase">{{index}}</h6>

    <VeeraCard class="card-clickable p-0 mb-3"
               v-for="person of persons"
               :key="person.id"
    >
      <a class="d-block px-3 py-2 mb-1 no-decoration" href="#" tabindex=""
         @click.stop.prevent="changePerson(person.value)">
        <strong>{{person.title}}</strong>
      </a>
    </VeeraCard>

  </div>

</div>
</template>

<script>
import mockData from '@/mockData'
import VeeraCard from '@/components/VeeraCard'
import _ from 'lodash'
import store from '../store'

export default {
  name: 'UserRoleSelectionPage',
  components: { VeeraCard },
  computed: {
    personCategories () {
      return _.groupBy(mockData.mockUserPersons, 'category')
    }
  },
  methods: {
    changePerson (newPerson) {
      store.changeCurrentPerson(newPerson)
      this.$router.push({ name: 'AccessRightsPage' })
    }
  }
}
</script>
