<template>
  <div class="veera-dropdown-container" ref="dropdown">
    <label for="exampleDropdown2" v-if="label">{{label}}</label>
    <div class="veera-input-container w-100"
         :class="{ open: open }"
    >
      <template v-if="selectOnly">
        <button type="button" class="veera-select select-only"
                id="lang-selection"
                @click="open = !open"
        >
          {{currentSelection.title}}
        </button>
      </template>
      <template v-else>
        <input type="text"
               role="presentation"
               autocomplete="off"
               @focus="open = true"
               @input="onChange"
               class="veera-select" id="exampleDropdown2"
               ref="searchInput"
               :placeholder="placeholder"
               :value="currentSelection && currentSelection.title"
        >
      </template>
      <div id="exampleDropdown2-menu"
           class="dropdown-menu right"
           :class="{ 'd-none': !open }"
           style="margin: 0; position: absolute; inset: 0px auto auto 0px;
           transform: translate(-2px, 38px);"
           data-popper-placement="bottom-end">
        <ul role="listbox">
          <template v-if="options.length > 0">
            <li v-for="option of options"
                class="dropdown-item"
                role="option" aria-label="Jane"
                :value="option.value"
                :key="option.value"
                @click="setSelectedOption(option)"
            ><span v-html="option.title"></span>
              <template v-if="option.description">
                <span class="d-block text-muted small" v-html="option.description"></span>
              </template>
            </li>
          </template>
          <template v-else>
            <li class="dropdown-item no-results">
              {{noResultsMessage ? noResultsMessage : 'Vasteid ei leitud'}}
            </li>
          </template>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VeeraDropdown',
  model: {
    prop: 'selectedOption',
    event: 'change'
  },
  props: ['label', 'id', 'options', 'selectedOption', 'selectOnly', 'autoFocus', 'placeholder', 'noResultsMessage'],
  data () {
    return {
      open: false,
      currentSelection: null
    }
  },
  created () {
    if (this.selectedOption) {
      this.setSelectedOption(this.selectedOption)
    } else {
      this.setSelectedOption()
    }
    if (this.autoFocus) {
      this.focusInput()
    }
    window.addEventListener('click', this.close)
  },
  beforeDestroy () {
    window.removeEventListener('click', this.close)
  },
  methods: {
    close (e) {
      if (!this.$refs.dropdown.contains(e.target)) {
        this.open = false
      }
    },
    setSelectedOption (option) {
      if (option && !option.disabled) {
        this.$emit('change', option)
        this.currentSelection = option
        this.open = false
      }
    },
    focusInput () {
      setTimeout(() => {
        this.$refs.searchInput.focus()
      }, 0)
    },
    onChange (e) {
      this.$emit('change', e.target.value)
    }
  }
}
</script>

