<template>
  <div>
    <template v-if="state.roleAddingSelectedPersonDetails">
      <div class="form-control h-auto d-flex align-items-center bg-light">
        <div>
          {{state.roleAddingSelectedPersonDetails.legalName ? state.roleAddingSelectedPersonDetails.legalName : (state.roleAddingSelectedPersonDetails.firstName + ' ' + state.roleAddingSelectedPersonDetails.lastName)}}
          ({{state.roleAddingSelectedPersonDetails.id}})
        </div>
        <VeeraTooltip v-if="state.roleAddingSelectedPersonDetails.description" class="d-inline-flex ml-2">
          {{state.roleAddingSelectedPersonDetails.description}}
        </VeeraTooltip>
        <div class="ml-auto my-n2 mr-n2">
          <button type="button"
                  class="c-btn c-btn--link c-btn--icon c-btn--icon-only c-btn--sm"
                  aria-label="Muuda"
                  @click="changeSelectedPerson()"
          >
            <i class="material-icons notranslate">edit</i>
          </button>
        </div>
      </div>
    </template>
    <template v-else>
      <VeeraDropdown :options="personOptions"
                     :selected-option="userSelectedPerson"
                     :auto-focus="autoFocusRequested"
                     @change="x => personChanged(x)"
                     placeholder="Sisesta isikukood või registrikood"
      />
    </template>
  </div>
</template>

<script>
import VeeraDropdown from '@/components/VeeraDropdown'
import mockData from '@/mockData'
import store from '@/store'
export default {
  name: 'SelectPerson',
  components: { VeeraDropdown },
  model: {
    prop: 'selectedPerson',
    event: 'change'
  },
  props: ['selectedPerson'],
  data () {
    return {
      state: store.state,
      autoFocusRequested: true,
      userSelectedPerson: null
    }
  },
  methods: {
    personChanged (newVal) {
      if (newVal.value) {
        this.getPersonDetails(newVal.value)
        this.$emit('change', newVal)
      }
    },
    changeSelectedPerson () {
      this.autoFocusRequested = true
      store.state.roleAddingSelectedPersonDetails = null
    },
    getPersonDetails (id) {
      store.state.roleAddingSelectedPersonDetails = mockData.getPersonById(id)
    }
  },
  computed: {
    personOptions () {
      return mockData.persons
        .filter(x => (x.id === '30303039816' || x.id === '46414160202' || x.id === 'EE23456789'))
        .map(person => {
          return {
            title: (person.legalName ? person.legalName : (person.firstName + ' ' + person.lastName)) + ' (' + person.id + ')',
            value: person.id
          }
        })
    }
  }
}
</script>
