import Vue from 'vue'
import VueRouter from 'vue-router'
import AccessRightsPage from '../views/AccessRightsPage'
import RoleDescriptionsPage from '@/views/RoleDescriptionsPage'
import UserRoleSelectionPage from '../views/UserRoleSelectionPage'
import HomePage from '../views/HomePage'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage
  },
  {
    path: '/volitused',
    name: 'AccessRightsPage',
    component: AccessRightsPage
  },
  {
    path: '/rollid',
    name: 'Roles',
    component: RoleDescriptionsPage
  },
  {
    path: '/vali-esindatav',
    name: 'UserRoleSelectionPage',
    component: UserRoleSelectionPage
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
